import React, { useState, useRef, useEffect } from "react";
import Input from "../../../components/common/input/Input";
import Button from "../../../components/common/button/Button";
import { FormSelectBox } from "../../../components/common/selectBox/BaseSelectBox";
import RegistCocktailList from "./RegistCocktailList";
import RegistBarImageList from "./RegistBarImageList";
import { styled } from "styled-components";
import { BAR_INFO, ButtonOptions } from "./ManageInfoOptions";
import { CocktailProps } from "../../../libs/interface/interfaceCocktail";
import { FORM_EVENT } from "../../../libs/interface/typeEvent";
import { formDataInstance } from "../../../libs/apis/axios";
import { useRecoilValue } from "recoil";
import { registCocktailListStateAtom, registCocktailImagesStateAtom } from "../../../recoil/barManageAtom";

export interface ManageBarProps {
  barName: string;
  barLocation: string;
  barLocationDetail: string;
  barMood: string;
  activatedCoverCharge: boolean;
  coverCharge: string;
  activatedDiscount: boolean;
  discount: string;
  barOpeningTime: string;
  barDetail: string;
  barPics: File[];
  cocktailList: CocktailProps[];
}

export const ManageInfo = () => {
  //  
  const formRef = useRef<any>({});

  //   
  const barPicsRef = useRef<File[]>([]);

  //   
  const registCocktailList = useRecoilValue(registCocktailListStateAtom);
  const registCocktailImages = useRecoilValue(registCocktailImagesStateAtom);
  const registCocktailRef = useRef<FormData>(null);

  useEffect(() => {
    console.log(registCocktailList);
    console.log(registCocktailImages);
  }, [registCocktailList]);

  const [showList, setShowList] = useState<string[]>([]); //  

  //  
  const handleSubmit = async (e: FORM_EVENT) => {
    e.preventDefault();

    if (barPicsRef.current.length < 4) {
      alert("  4  !");
      return;
    }

    const formData = new FormData(e.currentTarget);

    barPicsRef.current.forEach((item) => {
      formData.append("barPics", item, item.name);
    });

    const response = await formDataInstance.post("/admin/registBar", formData); // barUid
    registCocktail(response.data);
    return response.data;
  };

  //  
  const registCocktail = async (barId: number) => {
    const formData = new FormData();
    formData.append("barUid", new Blob([JSON.stringify(barId)], { type: "application/json" }));
    formData.append("cocktails", new Blob([JSON.stringify(registCocktailList)], { type: "application/json" }));
    registCocktailImages.forEach((item, idx) => {
      formData.append("cocktailPic", item);
    });

    for (const [key, value] of formData) {
      console.log(key, value);
    }

    const response = await formDataInstance.post("admin/registCocktail2", formData);
    console.log(response.data);
    return response.data;
  };

  return (
    <>
      <StyledForm onSubmit={handleSubmit} ref={formRef}>
        <h2 className="a11y-hidden"> </h2>

        <section>
          {/** idx, name */}
          {/** 0.  (barName) */}
          <StyledSectionBarInfo>
            <StyledH3></StyledH3>
            <Input {...BAR_INFO.NAME} />
          </StyledSectionBarInfo>
          {/** 1, 2.  , (barLocation, barLocationDetail) */}
          <StyledSectionBarInfo>
            <StyledH3></StyledH3>
            <FormSelectBox
              name="barLocation"
              data={[" ", " ", " "]}
              placeholder={""}
              nulltext={""}
            />
            <Input {...BAR_INFO.LOCATION_DETAIL} />
          </StyledSectionBarInfo>
          {/** 3.  (barMood) */}
          <StyledSectionBarInfo>
            <StyledH3></StyledH3>
            <FormSelectBox
              name="barMood"
              data={["#", "#", "#"]}
              placeholder={""}
              nulltext={""}
            />
          </StyledSectionBarInfo>
          {/** 4, 5. (activeCoverCharge, coverCharge) */}
          <StyledSectionBarInfo>
            <StyledH3></StyledH3>
            <FormSelectBox name="activatedCoverCharge" data={["", ""]} placeholder={""} nulltext={""} />
            <Input {...BAR_INFO.COVER_CHARGE} />
          </StyledSectionBarInfo>
          {/** 6, 7.  (activeDicount, discount) */}
          <StyledSectionBarInfo>
            <StyledH3></StyledH3>
            <FormSelectBox
              name="activatedCoverChargeOff"
              data={["", ""]}
              placeholder={""}
              nulltext={""}
            />
            <Input {...BAR_INFO.COVER_CHARGE_OFF} />
          </StyledSectionBarInfo>
        </section>
        <section>
          {/** 8.  (barOpeningTime) */}
          <StyledH3>     </StyledH3>
          <StyledSectionsBarDesc>
            <StyledTextarea
              name="barTime"
              placeholder="  Bar      
  "
            ></StyledTextarea>
            <StyledSpan>
              {`Ex. - 17:00-24:00
            - 15:00-24:00
          `}
            </StyledSpan>
          </StyledSectionsBarDesc>
          {/** 9.  (barDetail) */}
          <StyledSectionsBarDesc>
            <StyledH3> </StyledH3>
            <StyledTextarea
              name="barContents"
              placeholder="    . ( 50)"
            ></StyledTextarea>
            <StyledSpan>
              {`Ex.     .    50  .    ,   ?  
          `}
            </StyledSpan>
          </StyledSectionsBarDesc>
          {/** 10.  (barPics) */}
          <StyledSectionsBarDesc>
            <RegistBarImageList barPicsRef={barPicsRef} />
          </StyledSectionsBarDesc>
        </section>
        <section>
          <StyledH3>  &#40; 5&#41;</StyledH3>
          <RegistCocktailList registCocktailRef={registCocktailRef} />
        </section>

        <Button {...ButtonOptions} onClick={() => {}}></Button>
      </StyledForm>
    </>
  );
};

