import React, { useState, useEffect, useMemo } from "react";
import { styled } from "styled-components";
import Tag from "./Tag";
import { TagProps } from "../../libs/interface/interfaceCommon";

function pickRandom(arr: string[]) {
  const idx = Math.floor(Math.random() * arr.length);
  return arr[idx];
}

const TagList = () => {
  const levels = useMemo(() => ["", "", ""], []);
  const moods = useMemo(() => ["", "", "", "", "", "", "", "", "", "", ""], []);
  const citys = useMemo(
    () => ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""],
    []
  );
  const [tagOptions, setTagOptions] = useState<string[]>([]);

  function setCategory(value: string) {
    if (levels.includes(value)) {
      return "cocktail";
    } else if (moods.includes(value)) {
      return "barMood";
    } else if (citys.includes(value)) {
      return "barLocation";
    }
  }

  useEffect(() => {
    setTagOptions([pickRandom(levels), pickRandom(moods), pickRandom(citys)]);
  }, [levels, moods, citys]);
  return (
    <TagContainer>
      {tagOptions.map((item, idx) => {
        const tagOptions: TagProps = {
          typevariants: "primary",
          value: item,
          category: setCategory(item),
        };

        return <Tag {...tagOptions} key={idx} />;
      })}
    </TagContainer>
  );
};

export default TagList;

const TagContainer = styled.section`
  display: flex;
  justify-content: flex-start;
  gap: 10px;
  margin-bottom: 36px;
  padding: 0 20px;

  label {
    font-family: var(--font--Medium);
    font-size: 12px;
    padding: 6px 16px;
  }
`;
